@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 100%;
  min-height: 125px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  background-color: rgb(101, 6, 129);
  color: white;
  border: none;
}
.navbar h1 {
  color: white;
  font-size: 40px;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: white;
  font-size: 20px;
}
.navbar a:hover {
  color: #e21717;
}

/* Home */
.home__second {
  padding: 20px;
  display: flex;
  align-items: center;
  min-height: 110px;
  color: white;
  background-color: #25162c;
}

.home__second__text {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 19px;
  font-weight: 500;
}

.blog-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #18191a;
  width: 100%;
}

.blog-list__previews {
  background-color: #18191a;
  max-width: 400px;
  max-height: 700px;
  padding: 10px 0;
  padding-top: 0%;
  margin: 35px 30px;
  border-radius: 15px;
  box-shadow: -3px -3px 5px #18191a, 3px 3px 20px #00000070;
}

.blog-list__previews:hover {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
}

.blog-list__previews img {
  max-width: 100%;
  border: none;
  border-radius: 15px;
}

.blog-list__previews h2 {
  font-size: 25px;
  color: white;
  margin: 10px 10px;
}

.blog-list__previews p {
  color: white;
  margin: 10px 10px;
}
.blog-list__previews b {
  color: white;
}

.blog-list__previews a {
  margin: 10px 10px;
  text-decoration: none;
}

.blog-list__previews button {
  font-size: 15px;
  background-color: #e21717;
  border: none;
  width: 100px;
  height: 35px;
  margin: 5px 5px;
  margin-top: 20px;
  border-radius: 11px;
  color: white;
  display: inline-block;
  outline: none;
  cursor: pointer;
  transform: scale(1);
  transition: 0.5s;
}

.blog-list__previews button:hover {
  transform: scale(1) translateX(15px);
}

/* blog details page */

.blog-details {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 0px;
  padding: 10px 10px;
  background-color: #18191a;
  color: white;
}

.blog-details__content article {
  max-width: 725px;
  padding: 10px 10px;
  margin: 10px 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.blog-details__content img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  background-repeat: no-repeat;
}
.blog-details__content h2 {
  max-width: 100%;
  height: auto;
  font-size: 30px;
  margin: 30px 1px;
  padding: 10px 1px;
  color: white;
  border-bottom: white 1px solid;
}

.blog-details__content p {
  max-width: 100%;
  font-size: 20px;
  padding: 10px 1px;
  color: white;
  border-bottom: white 1px solid;
}
.blog-details__content div {
  margin: 50px 0px;
  font-size: 20px;
  color: white;
}
.blog-details__content button {
  background: #e21717;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* create new blog form */
.create {
  max-width: 100%;
  min-height: 100vh;
  text-align: center;
  background-color: #18191a;
  color: white;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 30px;
  color: white;
  margin-bottom: 30px;
  padding-top: 20px;
}
.create form {
  margin: 10px 200px;
  height: 100%;
}
.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 10px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
  display: block;
  background-color: #18191a;
  color: white;
}

.create textarea {
  min-height: 100px;
}
.create input:hover {
  border: 1px solid rgb(150, 13, 192);
}
.create textarea:hover {
  border: 1px solid rgb(150, 13, 192);
}
.create select:hover {
  border: 1px solid rgb(150, 13, 192);
}
.create button {
  background: #18191a;
  font-size: 20px;
  width: 150px;
  height: 50px;
  color: #fff;
  border: 1px solid white;
  margin: 10px;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
}

.create button:hover {
  background-color: #2f3133;
  border: 1px solid rgb(150, 13, 192);
}

footer {
  min-width: 100%;
  height: 200px;
  padding-top: 30px;
  padding-left: 10px;
  background-color: #2b3636;
  color: white;
}
